@media(min-height: 500px) {
    body {
        background: #2a2a2a;
        min-height: 100vh;
        overflow: hidden;
    }
}
@media (max-height: 1902px){
    body {
        background: #2a2a2a;
        min-height: 100vh;
        overflow: hidden;
    }
}
@media(max-width: 997px){
    .mobile{
        display: block;
    }
}

