.headdiv{
    display: flex;
    flex-direction: column;
    align-items: start;

    overflow: hidden;
}

/**NAVIGATION BAR**/
.navibar{
    background-color: rgba(0, 0, 0, 0.25);
    display: flex;
    flex-direction: row;
    justify-content: center;

    margin:0;
    padding: 0 0 0 0.5em;
    width: 100%;

}
.linkbar{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-content: center;

    margin: 0 0 0 0;
    padding: 0;

    width: 100%;

}
.pageTitle{
    align-self: center;
}

.nameNavItem{
    padding: 22px 15px;
    color:white;

    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    font-size: 1.2em;
    font-style: oblique;

    > div{
        text-align: left;
    }
}
#nameNavIcon{
    font-size: 2em;
    margin-right: 1em;
    margin-bottom: 5px;
}
#nameNavText{
    font-size: 1.5em;
}

 
.navitem{
    padding-right: 1em;
    padding-left: 1em;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    margin-right: 1em;
    width: fit-content;
    overflow: hidden;

    border-style: solid;
    border-width: 0px 0px 2px;
    border-color: white;

}
#homeNavItem:hover{
    border-color: #506d96;
    > #homeLink{
        color: #506d96;
    }
}
#projectsNavItem:hover{
    border-color: #8a4571;
    > #projectsLink {
        color: #8a4571;
    }
}

.navlink{
    color: white;
    text-decoration: none;
    font-size: 1.5em;
}





